<template>
  <el-menu :default-active="active" :collapse="collapse" router>
    <el-menu-item v-for="item in menuList" :index="item.path" :key="item.path">
      <i :class="item.icon"></i>
      <span slot="title">{{ item.label }}</span>
    </el-menu-item>
  </el-menu>
</template>

<script>
import { mapState } from "vuex"
import { menuListConstant } from "../../assets/script/constant"
export default {
  name: "Menu",
  computed: {
    ...mapState("menu", ["collapse"]),
    active() {
      return this.menuList.find(
        (item) => this.$route.fullPath.indexOf(item.path) === 0
      ).path
    }
  },
  data() {
    return {
      menuList: menuListConstant()
    }
  }
}
</script>

<style type="text/less" lang="less" scoped></style>
